import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {RwdPriceBox} from "eshop/modules/cart/components/entriesList/shared/PriceBoxes";
import {Trigger, Section} from "eshop/modules/core/components/ui/Expander";
import {LRow, LCol} from "./Utils";
import UniversalProductDetails from "./FixProductDetails";
import {GraphicCell,NameCell,PriceCell,InputCell} from "./modalFragments/FixInputFragments";
import Outline from "../../../../../core/enum/Outline"

export default class FixProductRadioBox extends Component {
    constructor(props){
        super(props);
    }

    getSectionHeader(selected,callback) {
        return (
            <div>
                <Trigger className="js-expander__trigger__nested u-inline-block large-offset-by-3 medium-offset-by-3 u-padding-all-l u-no-padding-top">
                    <span className="js-expander__trigger--show">{'Zwiń\u00a0szczegóły'}</span>
                    <span className="js-expander__trigger--hide">{'Szczegóły'}</span>
                </Trigger>
                {selected && (<a href="#" className="u-inline-block u-padding-right-l" onClick={callback}>Usuń</a>)}
            </div>
        );
    }

    render(){
        let icon = this.props.vas.thumbnailIcon ? this.props.vas.thumbnailIcon : "air-drop";
        let outlineStyle = this.props.vas.selected ? {outline: Outline.SELECTED} : {};
        return (
                    <div className="u-position-relative g-white1-bg is-selected">

                        {this.props.vas.selected &&
                            <div className="opl-selection-layer" aria-hidden="true">
                                <div className="opl-selection-layer__border"></div>
                                <div className="opl-selection-layer__checkmark"></div>
                            </div>
                        }

                        <LRow className="u-no-margin u-padding-all-l u-no-padding-bottom u-small-padding-all u-small-padding-top-l"
                            onClick={this.props.onSelectionChanged}>
                            <GraphicCell vas={this.props.vas}/>
                            <NameCell vas={this.props.vas}/>
                            <PriceCell vas={this.props.vas} net={this.props.net}/>
                            <InputCell vas={this.props.vas}/>
                        </LRow>
                        <Section header={this.getSectionHeader(this.props.vas.selected,this.props.onSelectionChanged)} contentClassName="js-expander__container__nested" headerClassName="u-position-relative u-small-text-right" className="l-row u-no-margin opl-section-outer--2">
                            <UniversalProductDetails vas={this.props.vas}/>
                        </Section>

                    </div>
            );
    }
}

FixProductRadioBox.propTypes = {
    vas: PropTypes.shape({
        title: PropTypes.string,
        id: PropTypes.string,
        paymentDescriptions: PropTypes.shape({
            monthlyPayments: PropTypes.array
        })
    }),
    idx: PropTypes.oneOfType([PropTypes.string,PropTypes.number]),
    onSelectionChanged: PropTypes.func
}

FixProductRadioBox.defaultProps = {
    inputType: "radio"
}

